/* Easy way to center a fieldset cause flex is broken on fieldset */
.FieldsetContainer {
  display: block;
  margin: 1.0em auto;
  text-align: center;
}

.FlexRow {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack:justify;
      justify-content:space-between;
}

.FieldsetContainer__Signin--Selected {
  background-color:red;
}



